body {
  background-color: #C8D0D2;
  margin: 0;
  display: grid;
  place-items: center;
  padding-top: 12%;
  font-family: 'Poppins', sans-serif;
}

.container {
  max-width: 1000px;
  margin: 1rem 2rem;
 border-color: red;
}

.header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  background-color: white;
  padding: 0 1.5rem;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, .3);
  border-radius: 2%;
  border-color: aqua;
}

.form-group {
  display: flex;
  flex-direction: column;
  margin: .5rem;
}

.form-group > label {
  color: #777;
  font-size: .75rem;
  margin-bottom: .25rem;
}

select,
input[type="number"] {
  padding: 1em .75em;
  border-radius: .3em;
  border: 1px solid #ddd;
  border-color: aqua;
}

.btn {
  background-color: hsl(200, 100%, 50%);
  color: white;
  padding: .5em 1em;
  border-radius: .3em;
  border: none;
  cursor: pointer;
}

.btn:hover {
  background-color: hsl(200, 100%, 40%);
}

.card-grid {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(auto-fill, minmax(500px, 1fr));
  gap: 1rem;

}

.card {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: .25rem;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, .3);
  background-color: rgb(207, 199, 199);
  transform-style: preserve-3d;
  transition: 150ms;
  cursor: pointer;
  transform: perspective(1000px) rotateY(var(--rotate-y, 0)) translateY(var(--translate-y, 0));
 
}

.cardContainer {
  background-image: url(https://cdn.pixabay.com/photo/2017/08/30/01/05/milky-way-2695569_1280.jpg);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: .25rem;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, .3);
  background-color: rgb(203, 226, 250);
  transform-style: preserve-3d;
  transition: 150ms;
  cursor: pointer;
  transform: perspective(1000px) rotateY(var(--rotate-y, 0)) translateY(var(--translate-y, 0));
 width: 750px;
 height: 300px;
}
.card:hover {
  --translate-y: -2px;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, .5);
}

.card.flip {
  --rotate-y: 180deg;
}

.card .front {
  left: 0;
}

.card .front,
.card .back {
  position: absolute;
  padding: 1rem;
  backface-visibility: hidden;
}

.card .back {
  transform: rotateY(180deg);
}

.flashcard-options {
  margin-top: .5rem;
}

.flashcard-option {
  margin-top: .25rem;
  color: #555;
  font-size: .75rem;
}

.flashcard-option:first-child {
  margin-top: 0;
}

.flashcard-list-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}




.pagination-container {
  display: flex;
  justify-content: center;

  margin-top: 1rem;
}

.pagination {
  
  display: inline-flex;
  list-style: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

.pagination li {
  margin: 0 0.5rem;
}

.pagination a {
  color: #000;
  padding: 0.3rem 0.6rem;
  border-radius: 3px;
  text-decoration: none;
  background-color: #fff;
  border: 1px solid #ddd;
}

.pagination a:hover {
  background-color: #f5f5f5;
}

.pagination .active a {
  background-color: #f5f5f5;
  color: #000;
  font-weight: bold;
}


.marks {
  margin-top: 10px;
  font-weight: bold;
}




.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.form-group {
  margin-bottom: 10px;
}

label {
  margin-right: 5px;
}

.error-message {
  color: red;
  margin-top: 10px;
}

.login-button {
  margin-top: 10px;
}


.login-data-box {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #f5f5f5;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}
